@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@700&display=swap);
#header > * {
  padding: 5px 15px;
}
#header > #scrollUpAnchor {
  padding: 0px;
  margin: 0px;
}
#header-welcome {
  background-color: var(--primary-color);
  letter-spacing: 0.1rem;
}
#header-welcome > h3 {
  color: var(--white-color);
  font-size: 1rem;
}
#header-welcome > p {
  color: var(--black-color);
  font-size: 0.8rem;
  font-weight: bold;
}
#header-pan {
  border: 1px solid var(--primary-color);
  border-radius: 25px;
  padding: 10px 15px;
  background-color: var(--white-color);
  font-size: 0.8rem;
}
#header-pan > a {
  padding-right: 25px;
}
#main-header > * {
  margin-right: 50px;
}
#main-header > *:last-child {
  margin-right: 0px;
}
#header-sm > a {
  margin: 5px;
  border-radius: 100%;
  border: 1px solid var(--primary-color);
  padding: 5px;
}
#header-gradient {
  display: none;
  background-image: linear-gradient(
    135deg,
    var(--secondary-color) 10%,
    var(--primary-color) 100%
  );
  height: 70px;
}
#header > div {
  position: relative;
}
#header-left > img {
  margin-right: 15px;
}
#search-bar {
  border: 1px solid var(--primary-color);
  border-radius: 25px;
  padding: 10px 15px;
  background-color: var(--white-color);
}
#search-bar > input {
  background-color: rgba(255, 255, 255, 0);
}
#search-bar > button {
  border: 0px none;
  background-color: rgba(255, 255, 255, 0);
  cursor: pointer;
}
#header-center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  letter-spacing: 0.2em;
}
#header-right > select {
  padding: 7px 3px;
  outline: none;
  border: 0px solid black;
  background-color: transparent;
}
#bg-button {
  display: none;
  cursor: pointer;
}
#navbar {
  background-color: var(--primary-color);
}
#mob-navbar {
  display: none;
  position: fixed;
  z-index: 3000;
  right: 100%;
  top: 0%;
  width: 100vw;
  min-height: 100vh;
  background-color: var(--primary-color);
  transition: 0.4s ease-in-out;
}
.fr-li {
  color: var(--white-color);
}
.fr-li:hover {
  color: var(--grey-color);
}
#mob-navbar > .fr-li > .fr-dropdown {
  display: none;
}
#exit-btn {
  position: absolute;
  z-index: 100;
  right: 15px;
  top: 15px;
  cursor: pointer;
}
#mb-menu-logo {
  position: absolute;
  z-index: 100;
  left: 15px;
  top: 15px;
  cursor: pointer;
}
.fr-li {
  list-style: none;
  padding: 5px 10px;
  text-transform: uppercase;
  position: relative;
  cursor: pointer;
}
.fr-li:hover > .fr-dropdown {
  opacity: 1;
  z-index: 300;
}
.fr-dropdown {
  position: absolute;
  z-index: -1;
  top: 100%;
  left: 50%;
  width: 500vw;
  padding: 25px 5px;
  transform: translateX(-50%);
  background-color: var(--white-color);
  opacity: 0;
  border-bottom: 1px solid var(--black-color);
}
.dropdownli {
  position: relative;
  list-style: none;
  color: var(--black-color) !important;
}
.dropdownli:hover {
  position: relative;
  list-style: none;
  color: var(--primary-color) !important;
  transition: 0.4s ease-in-out;
}
.dropdownli > a {
  color: var(--black-color) !important;
  font-size: 0.9rem;
  margin: 5px;
}
.dropdownli:hover > a {
  color: var(--primary-color) !important;
  transition: 0.4s ease-in-out;
}
.fr-li:hover {
  color: var(--black-color);
  transition: 0.4s ease-in-out;
}
@media screen and (max-width: 992px) {
  #navbar {
    display: none;
  }
  #mob-navbar {
    display: flex;
  }
  #mob-navbar > a {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  #mob-navbar > a > .fr-li, #mob-navbar > a > .fr-li:hover{
    color: var(--black-color);
  }
  #header-center,
  #header-left {
    display: none;
  }
  #bg-button {
    display: flex;
    position: fixed;
    z-index: 2000;
    left: 15px;
    top: 15px;
  }
  #header-pan {
    display: flex;
    position: fixed;
    z-index: 2000;
    right: 15px;
    top: 15px;
    border-radius: 15px;
    padding: 3px 7px;
    border: 1px solid black;
    height: 38px;
  }

  #main-header > * {
    margin-right: 0px;
  }
  #header-sm > a {
    display: none;
  }
  #header-gradient {
    display: flex;
  }
  .fr-li {
    background-color: var(--grey-color);
    padding: 15px 5px;
    margin: 5px 0px;
    width: 95%;
  }
  #header > * {
    padding: 0px 0px;
  }
}

#footer > div > *{
  padding: 10px;
}
.news-img {
  position: relative;
}
.news-no-img {
  position: relative;
}
.new-cta {
  position: absolute;
  z-index: 100;
  bottom: 10%;
  left: 50%;
  top: 50%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  transform: translate(-50%, -50%);
}
@media screen and (max-width: 992px) {
  .new-cta {
    position: absolute;
    z-index: 100;
    bottom: 3%;
  }
}

.fr-model>div {
  padding: 0px;
  padding-bottom: 25px;
  background-color: var(--white-color);
}

.fr-model>div>a {
  font-size: 0.9rem;
}

.fr-model>div>* {
  padding: 5px;
  font-size: 0.9rem;
}

@media screen and (max-width: 992px) {
  .fr-model {
    padding: 10px;
  }

  .fr-model>* {
    margin: 5px;
  }
}
#services{
    padding: 15px;
}
.service-img{
    padding: 25px;
}
.service-img > img {
    border-radius: 100%;
    border: 5px solid var(--primary-color);
}
#testimonials {
  padding: 50px 20px;
  position: relative;
  background-color: var(--secondary-color);
}
#testimonials > .slide {
  padding: 10px;
  font-size: 1.5em;
  cursor: pointer;
  position: absolute;
  z-index: 1000;
  top: 50%;
  transform: translateY(-50%);
}
#testimonials > .slide:first-child{
  left: 10px;
}
#testimonials > .slide:last-child {
  right: 10px;
}
.comment{
    margin: 10px;
    padding: 10px;
    background-color: var(--third-color);
}
.comment > * {
    margin: 10px;
    padding: 5px 5px;
}
.comment > img {
    border-radius: 100%;
}
@media screen and (max-width: 992px) {
  #testimonials {
    padding: 30px 0px;
  }
  .comment{
      width: 30%!important;
      padding: 2px;
      font-size: 0.5em;
      margin: 3px;
  }
  .comment > * {
      margin: 2px;
      padding: 2px 2px;
  }
  #testimonials > .slide{
    font-size: 1.2em;
  }
  #testimonials > .slide:first-child{
    left: 0px;
  }
  #testimonials > .slide:last-child {
    right: 0px;
  }
}

#catalog > h2 {
  font-size: 1.2rem;
  background-color: var(--secondary-color);
  padding: 15px 0px;
}
#catalog > section {
  padding: 25px 0px;
}
.fr-card {
  position: relative;
  padding: 50px;
  transition: 0.4s ease-in-out;
}
.fr-card > .container {
  position: relative;
}
.fr-card > .container > h3 {
  position: absolute;
  z-index: 1;
  bottom: 0%;
  left: 0%;
  width: 100%;
  padding: 15px 0px;
  background-color: rgba(235, 235, 235, 0.664);
  font-size: 1.1rem;
}
@media screen and (max-width: 1500px) {
  .fr-card {
    padding: 25px;
  }
  .fr-card > .container > h3 {
    font-size: 1.05rem;
  }
}
@media screen and (max-width: 992px) {
  #catalog > h2 {
    font-size: 0.9rem;
    background-color: var(--secondary-color);
    padding: 15px 0px;
  }
  .fr-card {
    padding: 10px;
  }
  .fr-card > .container > h3 {
    font-size: 0.8rem;
  }

  .fr-card > p {
    font-size: 0.8em;
  }
}


#scrollUp{
    position: fixed;
    cursor: pointer;
    right: 15px;
    bottom: 15px;
    z-index: 2000;
}
.fr-btn {
  padding: 5px 25px;
  text-transform: uppercase;
  font-size: 1em;
  border: 2px solid var(--primary-color);
  outline: none;
  cursor: pointer;
  font-weight: bold;
  background-color: var(--primary-color);
  transition: 0.4s ease-in-out;
  color: var(--white-color);
}
.fr-btn:hover {
  background-color: transparent;
  color: var(--primary-color);
}
.title > h3{
  text-transform: uppercase;
  position: relative;
  z-index: 100;
  padding: 5px;
  margin-bottom: 15px;
  font-size: 1.7rem;
}
.title > h3:before{
  position: absolute;
  content: "";
  height: 2px;
  width: 100%;
  left: 0%;
  bottom: 0%;
  background-color: var(--primary-color);
}
@media screen and (max-width: 992px) {
  .fr-btn {
    padding: 3px 15px;
    font-size: 0.9em;
  }
  .title > h3{
    font-size: 1rem;
  }
}

@font-face {
  font-family: "Lovelo";
  src: url("/sugar-paper/static/media/Lovelo Black.6da216b7.otf") format("woff");
}

html {
  --primary-color: #EB6297;
  --secondary-color: #FFC2C7;
  --third-color: #fee3e5;
  --grey-color: #F2EFF2;
  --white-color: #FCFFFF;
  --black-color: #252525;
  background-color: #FCFFFF;
  background-color: var(--white-color);
}
::after,
::before,
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  position: relative;
  max-width: 100vw;
  min-height: 100vh;
  background-color: #FCFFFF;
  background-color: var(--white-color);
  color: #252525;
  color: var(--black-color);
  font-family: "Open Sans", sans-serif;
  text-align: center;
  overflow-x: hidden;
}
a,
a:hover {
  text-decoration: none;
  color: #FCFFFF;
  color: var(--white-color);
  transition: 0.3s ease-in-out;
}
a:hover {
  color: #252525;
  color: var(--black-color);
}
img {
  max-width: 100%;
}
input {
  outline: none;
  border: none;
}
nav {
  margin: 0px;
}
h1,
h2,
h3,
h4,
h5 {
  font-family: "Open Sans";
}
/* width */
::-webkit-scrollbar {
  width: 8px;
  background-color: rgba(255, 255, 255, 0);
}
/* Track */
::-webkit-scrollbar-track {
  background-color: #EB6297;
  background-color: var(--primary-color);
  border-left: 1px solid #EB6297;
  border-left: 1px solid var(--primary-color);
  border-right: 1px solid #EB6297;
  border-right: 1px solid var(--primary-color);
}
/* Handle */
::-webkit-scrollbar-thumb {
  background-color: #252525;
  background-color: var(--black-color);
  border-radius: 205px;
  cursor: pointer;
}

